html, body {
  height: 100%;
  margin: 0;
  scroll-behavior: smooth;
}

.app-container {
  display: flex; /* Ensures elements inside it are arranged horizontally */
  height: 100%; /* Fill the whole viewport */
}
.page-wrapper {
  margin-left: 250px;
  flex-grow: 1;
  overflow: auto;
  transition: margin-left 0.3s ease;
}


@media (max-width: 768px) {
  .page-wrapper {
    margin-left: 0;
  }
}
.success-message {
  background-color: #e6ffe6; /* soft green background */
  border: 1px solid #6cc070a8; /* matching green border */
  border-radius: 12px;
  padding: 20px;
  margin: 20px 10px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  animation: fadeIn 0.6s ease-in-out;
}

.success-message p {
  font-size: 14px;
  font-weight: 500;
  color: #2e7d32;
  margin: 0;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}