.project-card {
    width: 300px;
    border-radius: 7px;
    overflow: hidden;
    border: #dddddd 1px solid;
    margin: 15px;
    transition: 0.4s transform ease;
  }
.project-card:hover {
    transform: translateY(-7px);
  }
  
  
  .project-image {
    width: 100%;
    height: 180px;
    object-fit: cover;
  }
  
  .project-content {
    padding: 0px 0px 10px 10px;
  }
  
  .project-title {
    font-size: 18px;
    margin-bottom: 8px;
    margin-top: 5px;
  }
  
  .project-description {
    font-size: 16px;
    color: #555;
  }

  .tech-stack {
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
    margin-top: 10px;
  }
  
  .tech-pill {
    background-color: #fbfbfb;
    border-radius: 9999px;
    padding: 4px 10px;
    font-size: 14px;
    color: #333;
    border: 1px solid #ccc;
  }
  
  