  /* Individual item styling */
  .sidebar-item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    text-decoration: none;
    color: #333;
    padding: 10px;
    border: 1px solid #acbec5;
    border-radius: 5px;
    transition: background-color 0.5s ease;
  }
  
  /* Hover effect for better interaction */
  .sidebar-item:hover {
    background-color: #acd2d63a;
  }
  
  /* Image styling within the sidebar items */
  .sidebar-item-image {
    margin-right: 10px;
    width: 24px;
    height: auto;
  }
  
  /* Title styling */
  .sidebar-item-title {
    font-size: 16px;
    font-weight: 500;
  }
